.news_compony {
    margin-top: 100px;
    width: 100%;
}

.news_compony .contant {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 20px;
}

.news_compony .news_compony_block {
    margin-top: 50px;
    margin-bottom: 100px;
    width: 100%;
    height: 470px;
    border-radius: 30px;
    padding: 25px;
    background: var(--grayW);
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.news_compony .news_compony_block .image {
    width: 100%;
    height: 200px;
    object-fit: cover;
    object-position: center;
    border-radius: 15px;
}

.news_compony .news_compony_block .title {
    font-size: 18px;
    font-weight: 400;
    color: var(--black);
}

.news_compony .news_compony_block p {
    font-size: 14px;
    font-weight: 300;
    color: #575757;
}

.news_compony .news_compony_block .date {
    font-size: 13px;
    font-weight: 300;
    color: var(--gray);
}

.news_compony .news_compony_block .flex {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

@media (max-width: 600px) {
    .news_compony {
        margin-top: 50px;
    }

    .news_compony .contant {
        margin-top: 16px;
        display: grid;
        grid-template-columns: 1fr;
        gap: 12px;
    }

    .news_compony .news_compony_block {
        margin: 0;
        padding: 16px;
        height: 100%;
    }

    .news_compony .news_compony_block .image {
        height: 160px;
    }

    .news_compony .news_compony_block .title {
        font-size: 16px;
    }

    .news_compony .news_compony_block p {
        font-size: 14px;
    }

    .news_compony .flex img {
        width: 30px;
        height: 30px;
    }
}