.icons_componi {
    margin-top: 100px;
    width: 100%;
}

.icons_componi .slide_image {
    padding: 15px;
}

.icons_componi .slide_image img {
    width: 100%;
    height: 100px;
}

.slick-prev:before {
    font-size: 24px;
    color: var(--gray);
}

.slick-next:before {
    font-size: 24px;
    color: var(--gray);
}

@media (max-width: 600px) {
    .icons_componi {
        margin-top: 50px;
    }
}