* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Rubik, Gilroy, sans-serif;
}

body {
    background: var(--grayW);
}

.container {
    margin: 0 auto;
    max-width: 1280px;
    width: 90%;
    background: transparent;
}

:root {
    --grayW: #F5F7FA;
    --gray: #9F9F9F;
    --gblue: #00ADEE;
    --blue: #0F6EFE;
    --black: #141416;
    --white: #fdfdfd;
}